@import url('https://fonts.googleapis.com/css2?family=Alkatra:wght@400..700&display=swap');
.modalContentClass
{
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    background-color: #512da8;
    border-radius: 10px;
    padding: 0px;
    width: 520px;
}
.modalOverlayClass
{
    position: fixed;
    z-index: 2;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.8);
}
.form
{
    font-family: "Alkatra", system-ui;
    font-optical-sizing: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    color: white;
    margin: 20px;
    box-sizing: border-box;
}
.postHead
{
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.5px;
}
.inputContainer
{
    align-items: center;
    width: 100%;
    display: grid;
    grid-template-columns: 23% 75%;
    column-gap: 1%;
}
.inputContainer label
{
    text-align: right;
}
.inputContainer input , .inputContainer textarea , .inputContainer select
{
    background-color: #9364da;
    color: white;
    border: none;
    padding: 10px 15px;
    font-size: 17px;
    outline: none;
}
.inputContainer input::placeholder , .inputContainer textarea::placeholder
{
    color: bisque;
}
.inputContainer textarea
{
    resize: vertical;
    padding: 5px 10px;
    max-height: 200px;
}
.btnContainer
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    padding: 20px 0px;
}
.btnContainer button
{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    font-size: 20px;
    border-radius: 20px;
    cursor: pointer;
    color: white;
    border: none;
}
.btnContainer button:first-child
{
    background-color: orange;
    gap: 10px;
    font-weight: 600;
    box-shadow: 0px 0px 13px 6px orange;
}
.btnContainer button:first-child:hover
{
    box-shadow: 0px 0px 13px 6px violet;
    background-color: violet;
}
.btnContainer button:last-child
{
    background-color: orangered;
}
.btnContainer button:last-child:hover
{
    box-shadow: 0px 0px 13px 6px orangered;
}
@media only screen and (width <= 717px)
{
    .modalContentClass{width: 73%;}
}
@media only screen and (width <= 612px)
{
    .modalContentClass{width: 80%;}
    .form
    {
        gap:15px;
        margin-left: 4px;
        margin-right: 10px;
    }
}
@media only screen and (width <= 470px)
{
    .modalContentClass{width: 90%;}
    .inputContainer input , .inputContainer textarea , .inputContainer select{font-size: 3.1cqi;}
    .postHead{font-size: 6.2cqi;}
    .btnContainer button{font-size: 4cqi;}
}
@media only screen and (width <= 350px)
{
    .form{margin-right: 5px;}
}